import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'cliente-dados',   templateUrl: 'cliente_dados.component.html' }) export class ClienteDadosComponent implements OnInit {       displayMode: ClienteDadosDisplayModeEnum;     displayModeEnum = ClienteDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = ClienteDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = ClienteDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = ClienteDadosDisplayModeEnum.Edit;           break;       }     }  }  enum ClienteDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


